// json query for new

export async function findAll() {
    // const decoder = new TextDecoder('gbk');
    let response = require('@/assets/data/news.json');
    // console.log(1212, response);
    
    // let data = await response.json();

    console.log(response);
        
    return response["news"].reverse();
}

export async function findByNumber(num) {
    // const decoder = new TextDecoder('gbk');
    let response = require('@/assets/data/news.json');
    // let data = await response.json();

    console.log(response["news"].slice(-5));

    let count = -1 * num;
    return response["news"].slice(count).reverse();
}

export async function findById(id) {
    let response = require('@/assets/data/news.json');
    // let data = await response.json();

    console.log(response["news"]);

    return response["news"][id];
}
